html {
  box-sizing: border-box;
  min-height: 100%;
  scrollbar-gutter: stable;
  scroll-behavior: smooth;
}

*,
::before,
::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

:where(ul,ol):where([class]) {
  padding-left: 0;
}

body,
:where(blockquote,figure,fieldset):where([class]) {
  margin: 0;
}

:where(h1,h2,h3,h4,h5,h6,p,ul,ol,dl):where([class]) {
  margin-block: 0;
}

:where(dd[class]) {
  margin-left: 0;
}

:where(fieldset[class]) {
  padding: 0;
  border: none;
}

:where(ul[class]) {
  list-style: none;
}

:where(address[class]) {
  font-style: normal;
}

p {
  margin: 0;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

input,
textarea,
select,
button {
  font: inherit;
}

body {
  min-height: 100%;
  line-height: 1.5;
}

button,
label {
  cursor: pointer;
}

svg * {
  transition-property: fill,stroke;
}

:where(table) {
  border-collapse: collapse;
  border-color: currentColor;
}

