.nav {
  transition: all .3s ease-in-out;
  z-index: 2;
}

.nav__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  column-gap: min(80px,7vw);
}

.nav .nav__item--reg {
  display: none;
}

.nav__link {
  position: relative;
  padding: 13px 0;
  font-size: 18px;
  white-space: nowrap;
  transition: all .3s ease-in-out;
  background: linear-gradient(72deg, #a3a3a3 60%, #ffa228 40%, #ff6432 100%);
  color: rgba(0,0,0,0);
  background-clip: text;
  background-size: 300% 300%;
  background-position: 0% 50%;
}

.nav__link::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transition: all .3s ease-in-out;
  width: 6px;
  height: 6px;
  translate: -50%;
  border-radius: 50%;
  opacity: 0;
  visibility: hidden;
  background: linear-gradient(72deg, #ffa228 0%, #ff6432 100%);
}

.nav__link:hover,
.nav__link:focus-visible {
  background-position: 100% 50%;
  outline: none;
}

.nav__link:hover::after,
.nav__link:focus-visible::after {
  opacity: 1;
  visibility: visible;
}

.nav__link--active {
  background: linear-gradient(72deg, #ffa228 0%, #ff6432 100%);
  color: rgba(0,0,0,0);
  background-clip: text;
  background-size: 100% 100%;
  background-position: 0% 50%;
}

.nav__link--active::after {
  opacity: 1;
  visibility: visible;
}

.nav__link--active:hover,
.nav__link--active:focus-visible {
  background-position: 0% 50%;
  outline: none;
}

.nav__link--active:hover::after,
.nav__link--active:focus-visible::after {
  opacity: 1;
  visibility: visible;
}

