.burger {
  display: none;
  position: relative;
  background: none;
  padding: 0;
  border: none;
  width: 28px;
  height: 21px;
  z-index: 3;
}

.burger--cancel .burger__line {
  width: 0%;
  margin-left: 50%;
}

.burger--cancel .burger__line::before {
  transform: translateY(9.5px) rotateZ(-45deg);
}

.burger--cancel .burger__line::after {
  transform: translateY(-9.5px) rotateZ(45deg);
}

.burger__line {
  display: block;
  width: 100%;
  border-radius: 10px;
  height: 2px;
  background: linear-gradient(72deg, #ffa228 0%, #ff6432 100%);
  transition: all .3s ease-in;
}

.burger__line::before,
.burger__line::after {
  content: "";
  transition: all .3s ease-in;
  background: linear-gradient(72deg, #ffa228 0%, #ff6432 100%);
  border-radius: 10px;
  width: 100%;
  height: 2px;
}

.burger__line::before {
  position: absolute;
  top: 0;
  left: 0;
}

.burger__line::after {
  position: absolute;
  bottom: 0;
  left: 0;
}

